.header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 40px;
	height: 110px;
	background: linear-gradient(to bottom, #c4d1da, #dbe3e9);
	&__logo {
		flex: 1 1 30%;
		&__link {
			display: block;
			max-width: 225px;

			margin-top: 15px;
			img {
				background-position: 50% 50%;
				background-size: cover;
				width: 100%; } } } }

.header__navigation {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: center;
	flex: 1 1 70%;
	height: 100%; }

.header__client {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 20%;
	position: relative;
	height: 31px; }

.header__search {
	position: relative;
	top: -15px; }

#search_input {
	position: absolute;
	display: block;
	display: none;
	right: 0;
	top: 0;
	color: $blue_white;
	font-size: 14px;
	font-family: inherit;
	height: 31px;
	width: 280px;
	background: $clor_bg;
	padding: 5px 25px 5px 16px;
	// tranthition: 0.3s
	opacity: 0;
	z-index: 3; }

#over_white {
	z-index: 2;  //пoдлoжкa дoлжнa быть выше слoев элементoв сaйтa, нo ниже слoя мoдaльнoгo oкнa
	position: fixed;  //всегдa перекрывaет весь сaйт
	background-color: #fff;  //чернaя
	opacity: 0;  //нo немнoгo прoзрaчнa
	-moz-opacity: 0;  //фикс прозрачности для старых браузеров
	filter: alpha(opacity=80);
	width: 100%;
	height: 100%;  //рaзмерoм вo весь экрaн
	top: 0;  //сверху и слевa 0, oбязaтельные свoйствa!
	left: 0;
	cursor: pointer;
	display: none; }  //в oбычнoм сoстoянии её нет)

.icon-search {
	position: absolute;
	right: 8px;
	top: 6px;
	width: 16px;
	height: 19px;
	cursor: pointer;
	transition: 0.3s;
	fill: #56849d;
	z-index: 4;
	&:hover {
		fill: #ee9f32; } }

.header__login {
	font-size: 14px;
	color: #56849d;
	background: inherit;
	&:hover {
		color: #2c5b72; } }

.header__length {
	background: inherit;
	border: none;
	cursor: pointer;
	color: #56849d;
	font-size: 14px;
	&:hover {
		color: $blue; }
	option {
		background: #cad7df;
		cursor: pointer;
		outline: none; } }

.toggle-menu {
	display: none;
	width: 35px;
	height: 27px;
	cursor: pointer;
	background: none; }

.toggle {
	display: block;
	content: '';
	width: 100%;
	height: 5px;
	background: $blue;
	border-radius: 4px;
	margin-bottom: 4px;
	&:last-child {
		margin-bottom: 0; } }

.header__menu {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	width: 100%;
	margin-bottom: 5px;
	margin-top: 10px;
	&__item {
		margin-left: 20px; }
	&__link {
		color: #56849d;
		font-size: 14px;
		text-transform: uppercase;
		transition: color 0.3s;
		&:hover {
			color: $orange; } } }
