body {
  min-width: 320px;
  background: #fff;
  font-family: "Gotham", "Open Sans", sans-serif;
  font-weight: 500;
}

h1 {
  font-size: 40px;
  color: #2e5b72;
}

h2 {
  font-size: 22px;
  color: #2e5b72;
}

h3 {
  font-size: 16px;
  color: #2e5b72;
  padding-bottom: 5px;
}

.pagewrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

.main {
  height: 100%;
  background-color: #dce4ea;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 40px;
  height: 110px;
  background: linear-gradient(to bottom, #c4d1da, #dbe3e9);
}

.header__logo {
  flex: 1 1 30%;
}

.header__logo__link {
  display: block;
  max-width: 225px;
  margin-top: 15px;
}

.header__logo__link img {
  background-position: 50% 50%;
  background-size: cover;
  width: 100%;
}

.header__navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  flex: 1 1 70%;
  height: 100%;
}

.header__client {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 20%;
  position: relative;
  height: 31px;
}

.header__search {
  position: relative;
  top: -15px;
}

#search_input {
  position: absolute;
  display: block;
  display: none;
  right: 0;
  top: 0;
  color: #96b4c4;
  font-size: 14px;
  font-family: inherit;
  height: 31px;
  width: 280px;
  background: #e5edf1;
  padding: 5px 25px 5px 16px;
  opacity: 0;
  z-index: 3;
}

#over_white {
  z-index: 2;
  position: fixed;
  background-color: #fff;
  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}

.icon-search {
  position: absolute;
  right: 8px;
  top: 6px;
  width: 16px;
  height: 19px;
  cursor: pointer;
  transition: 0.3s;
  fill: #56849d;
  z-index: 4;
}

.icon-search:hover {
  fill: #ee9f32;
}

.header__login {
  font-size: 14px;
  color: #56849d;
  background: inherit;
}

.header__login:hover {
  color: #2c5b72;
}

.header__length {
  background: inherit;
  border: none;
  cursor: pointer;
  color: #56849d;
  font-size: 14px;
}

.header__length:hover {
  color: #2e5b72;
}

.header__length option {
  background: #cad7df;
  cursor: pointer;
  outline: none;
}

.toggle-menu {
  display: none;
  width: 35px;
  height: 27px;
  cursor: pointer;
  background: none;
}

.toggle {
  display: block;
  content: '';
  width: 100%;
  height: 5px;
  background: #2e5b72;
  border-radius: 4px;
  margin-bottom: 4px;
}

.toggle:last-child {
  margin-bottom: 0;
}

.header__menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  margin-bottom: 5px;
  margin-top: 10px;
}

.header__menu__item {
  margin-left: 20px;
}

.header__menu__link {
  color: #56849d;
  font-size: 14px;
  text-transform: uppercase;
  transition: color 0.3s;
}

.header__menu__link:hover {
  color: #ee9f32;
}

@media screen and (max-width: 1023px) {
  .pagewrap {
    width: 100%;
  }
  .header__navigation {
    display: none;
  }
  .toggle-menu {
    display: block;
  }
}

