.slider__video__wrap {
	position: relative;
	width: 100%;
	height: 81.5%;
	display: none;
	z-index: 6;
	position: fixed;  //чтoбы oкнo былo в видимoй зoне в любoм месте
	top: 116px;  //oтступaем сверху 45%, oстaльные 5% пoдвинет скрипт
	left: 0;  //пoлoвинa экрaнa слевa
	margin-top: -242px;
	margin-left: 0px;  //тут вся мaгия центрoвки css, oтступaем влевo и вверх минус пoлoвину ширины и высoты сooтветственнo =)
	display: none;  //в oбычнoм сoстoянии oкнa не дoлжнo быть
	opacity: 0;  //пoлнoстью прoзрaчнo для aнимирoвaния
	padding-top: 18px;
	background-color: #dce4ea; }

.slider__video {
	width: 80%;
	height: 100%;
	margin: 0 auto;
	iframe {
		width: 100%;
		height: 100%;
		background-size: cover; } }

.slider__video__close {
	position: absolute;
	top: 15px;
	right: 50px;
	cursor: pointer;
	display: block;
	width: 48px;
	height: 48px;
	border-radius: 50%;
	background-color: $orange;
	transition: background-color 0.3s;
	&:hover {
		background-color: $blue; } }


.icon-close {
	display: block;
	width: 20px;
	height: 20px;
	margin: 28%;
	text-align: center;
	fill: #fff; }


.slider__button {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 300px;
	height: 40px;
	margin: 0 auto; }

.slider__button__left {
	display: flex;
	align-items: center;
	justify-content: space-between; }

.button__hr {
	display: inline-block;
	content: '';
	height: 0;
	width: 69px;
	border-top: 1px dotted $blue; }

.button__block1 {
	position: relative;
	height: 38px;
	width: 38px;
	margin-left: 10px;
	border-radius: 50%;
	border: 1px solid $blue;
	background-color: $clor_bg;
	cursor: pointer;
	&:after {
		position: absolute;
		display: block;
		content: '';
		width: 3px;
		height: 3px;
		background-color: $blue;
		border-radius: 50%;
		top: 47%;
		left: 47%;
		z-index: 2; } }

.slider__button__right {
	display: flex;
	align-items: center;
	justify-content: space-between; }

.button__block2 {
	position: relative;
	height: 20px;
	width: 20px;
	margin-right: 10px;
	border-radius: 50%;
	border: 1px solid $blue;
	background-color: $clor_bg;
	cursor: pointer;
	&:after {
		position: absolute;
		display: block;
		content: '';
		width: 3px;
		height: 3px;
		background-color: $blue;
		border-radius: 50%;
		top: 45%;
		left: 45%;
		z-index: 2; } }


.button__inform {
	font-size: 12px;
	color: $blue; }

//Start popup slider click
.slider__popup__click {
	position: relative;
	position: fixed;
	display: none;
	width: 100%;
	height: 82%;
	top: 44.5%;
	left: 0;
	margin-top: -208px;
	z-index: 4;
	background: rgba(220, 228, 234, 0.8);
	opacity: 0;
 }	// background: linear-gradient(to bottom, #cad7de, #eaeff2)

.slider__popup__content {
	position: relative;
	margin: 0;
	width: 890px;
	height: 516px;
	top: 50%;
	left: 50%;
	margin-right: -50%;
	transform: translate(-50%, -50%);
	&::after {
		display: block;
		position: absolute;
		content: '';
		width: 50px;
		height: 260px;
		margin: 0;
		top: 50%;
		left: 73%;
		margin-right: -50%;
		transform: translate(-50%, -50%);
		background-image: url(../img/ellipse_right.png);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		z-index: 4; }
	&::before {
		display: block;
		position: absolute;
		content: '';
		width: 50px;
		height: 260px;
		margin: 0;
		top: 50%;
		left: 26%;
		margin-right: -50%;
		transform: translate(-50%, -50%);
		background-image: url(../img/ellipse_left.png);
		background-repeat: no-repeat;
		background-position: center;
		z-index: 4; } }

.slider__popup__close {
	position: absolute;
	top: 85px;
	right: 36%;
