.main__slider {
	height: 480px; }

.slide {
	display: inline-block;
	height: 480px;
	&:hover {
		.slide__direction {
			color: #ee9f32;
			cursor: pointer; }
		.slide__link {
			border-color: transparent;
			&:before {
				opacity: 1; }
			img {
	 			transform: scale(1.1); } } } }

.slide__bg {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	height: 100%;
	width: 100%;
	color: #396f8c;
	text-align: center;
	cursor: pointer;
	filter: blur(2px);
	transition: color 0.3s;
	&:hover {
		filter: blur(0px); } }

.slide__link {
	position: relative;
	max-width: 335px;
	max-height: 335px;
	border-radius: 50%;
	margin: 30px;
	border: 1px solid $blue;
	transition: border-color 0.3s, transform 0.3s;
	img {
		max-width: 100%;
		max-height: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: left top;
		padding: 10px;
		transition: transform 0.3s; }
	&:before {
		position: absolute;
		display: block;
		content: '';
		width: 112%;
		height: 120%;
		background-image: url(../img/vector-object400.png);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		transition: opacity 0.3s;
		top: -28px;
		left: 0;
		opacity: 0; } }

.slide__direction {
	display: block;
	font-size: 22px;
	width: 170px;
	margin: 15px auto 10px;
	text-transform: uppercase;
	text-align: center; }
